.App {
  height: 100vh;
  width: 100vw;
  margin: 0;
  font-family: "Mulish", sans-serif;
  box-sizing: border-box;
}

.fakeheader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 40px;
  box-sizing: border-box;
  margin: 0;
}

.logo {
  max-width: 90px;
}

.bottom {
  position: relative;
  margin: 0;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  /* background-image: linear-gradient(135deg, #010203 10%, #002b82 100%); */
  background-image: url("./assets/upscaled.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position-x: right;
  align-items: center;
  justify-content: center;
  padding: 50px 40px;
  color: white;
  z-index: -1;

  @media (min-width: 860px) {
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
  }
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(
    0,
    0,
    0,
    0.4
  ); /* Adjust the last value (0.5) for transparency */
  z-index: -1;

  @media (min-width: 860px) {
    display: none;
  }
}
.content {
  display: flex;
  align-items: center;
  flex-direction: column;
  box-sizing: border-box;
  margin: 0;
  @media (min-width: 860px) {
    align-items: flex-start;
    max-width: 400px;
    padding-top: 20px;
  }
}

.textbig {
  font-size: 36px;
  margin-bottom: 40px;
  /* font-size: 22px;
  @media (min-width: 860px) {
    font-size: 36px;
  } */
}

.textsmall {
  font-size: 20px;
  margin-bottom: 40px;
  /* font-size: 16px;
  @media (min-width: 860px) {
    font-size: 20px;
  } */
}

.actionwhite {
  background-color: white;
  margin-bottom: 40px;
  font-weight: 800;
  font-size: 20px;
  line-height: 20px;
  border-radius: 30px;
  padding: 20px 30px;
  cursor: pointer;
  border: none;
  outline: inherit;
}

.actionblack {
  background-color: black;
  color: white;
  font-weight: 800;
  font-size: 16px;
  line-height: 20px;
  border-radius: 24px;
  padding: 10px 20px;
  cursor: pointer;
  border: none;
  outline: inherit;
}

.actionblack:hover {
  opacity: 0.5;
}

.actionwhite:hover {
  opacity: 0.5;
}

.phoneImg {
  max-width: 200px;
}
